import CommonFooter from "@/components/CommonFooter.vue";
export default {
  components: {
    CommonFooter
  },
  name: "Download",

  data() {
    return {
      activeTab: "gateway",
      config: {
        products: {
          gateway: {
            label: "INFINI Gateway",
            version: "1.6.0",
            number: 700,
            size: "7M",
            build_date: "12-Aug-2022 14:25",
            release_notes_url: "https://gateway.infinilabs.com/zh/docs/release-notes/",
            docker: {
              repo: "https://hub.docker.com/r/infinilabs/gateway"
            },
            selectedOS: "linux",
            selectedPlatform: "amd64"
          },
          console: {
            label: "INFINI Console",
            version: "0.3.1",
            number: 748,
            size: "13M",
            build_date: "25-Jul-2022 01:58",
            release_notes_url: "https://console.infinilabs.com/zh/docs/release-notes/",
            docker: {
              repo: "https://hub.docker.com/r/infinilabs/console"
            },
            selectedOS: "linux",
            selectedPlatform: "amd64"
          }
        },
        os: {
          linux: {
            label: "Linux",
            platform: ["386", "amd64", "arm5", "arm6", "arm64", "arm7", "mips", "mips64", "mips64le"],
            packageType: "tar.gz"
          },
          mac: {
            label: "Mac",
            platform: ["amd64", "arm64"],
            packageType: "zip"
          },
          windows: {
            label: "Windows",
            platform: ["386", "amd64"],
            packageType: "zip"
          }
        }
      }
    };
  },

  mounted() {
    let query = this.$route.query;
    let products = Object.keys(this.config.products);

    if (query.tab && products.indexOf(query.tab)) {
      this.activeTab = query.tab;
    }
  },

  methods: {
    onTabChange(tab) {
      this.activeTab = tab;
      let newQuery = JSON.parse(JSON.stringify(this.$route.query));
      newQuery.tab = tab;
      this.$router.replace({
        query: newQuery
      });
    },

    osChange(event) {
      this.config.products[this.activeTab].selectedPlatform = this.config.os[event.target.value].platform[0];
    },

    goDownload() {
      //https://release.infinilabs.com/console/stable/console-0.3.1_SNAPSHOT-748-mac-amd64.zip
      let current = this.config.products[this.activeTab];
      let url = `https://release.infinilabs.com/${this.activeTab}/stable/${this.activeTab}-${current.version}-${current.number}-${current.selectedOS}-${current.selectedPlatform}.${this.config.os[current.selectedOS].packageType}`;
      location.href = url;
    }

  }
};